import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch, useStore } from 'react-redux';
import allActions from '../redux/actions';
import { ADD_ELE, TOGGLE_ELE, SET_FILTER, SET_STATE } from "../redux/actionTypes";
import axios from "axios";
import Cookies from "universal-cookie";
const cookies = new Cookies();

export default function Auth(){


    const uId = useSelector(state => state.userState.uId);
    let [eblan, setEblan] = useState();

    useEffect(() => {
    const configuration = {
        method: "get",
        url: "http://localhost:8080/account/auth",
        withCredentials: true,
    };

    // make the API call
        if(uId){
            axios(configuration)
            .then((response) => {
                setEblan(response.data.uId);
            })
            .catch((error) => {
                console.log(error)
            });
        }

    }, [uId]);
    return(
        <p>You are {eblan}</p>
    )
}